import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { routes } from 'src/app/core/helpers/routes';
import { environment } from '../../../../environments/environment';
@Component({
  selector: 'app-signin-3',
  templateUrl: './signin-3.component.html',
  styleUrl: './signin-3.component.scss',
})
export class Signin3Component {
  public routes = routes;
  public email: string = '';
  public passwordInput: string = '';
  public passwordVisible: boolean = false;
  
  constructor(private router: Router) {}
  
  async navigation() {

    const formData = new FormData();
    formData.append('username', this.email);
    formData.append('password', this.passwordInput);
    
      
      try {
        const response = await fetch(`${environment.BACKEND_URL}/auth/admin/login`, {
          method: 'POST',
          body: formData
        })
        if(response.status!=200)
          alert('Invalid email or password');
        else{
          this.router.navigate([routes.adminDashboard]);
          const data = await response.json(); 
          localStorage.setItem('authToken', data.results.access_token);
          // console.log(data.results.access_token)
        }
      } catch (error) {
        console.error('Login failed:', error);
        alert('Invalid email or password');
      }
  }

  public togglePassword() {
    this.passwordVisible = !this.passwordVisible;
  }
}
